import React, { useState } from 'react';
import {  useDispatch, useSelector } from 'react-redux';
import {
  
  
  selectItems,
  UpdateCartAsync,
  DeleteCartAsync,
  selectCartStatus

} from './CartSlice';

import { Link } from 'react-router-dom';
import {  MagnifyingGlass } from 'react-loader-spinner';
import { Navigate } from 'react-router-dom';
import { discountedPrice } from '../../app/constant';


import Modal from '../comman/Modal';

 

export  default function Counter() {
  
  const items=useSelector(selectItems)
  const dispatch=useDispatch()
  const status=useSelector(selectCartStatus)
  const [open, setOpen] = useState(true)
  const [openModal,setOpenModal]=useState(null)
  const totalAmount=items.reduce((amount,item)=>discountedPrice(item.product)*item.quantity +amount,0)
  const totalItems=items.reduce((total,item)=>item.quantity +total,0)
  const handleRemove=(e,id)=>{
    dispatch(DeleteCartAsync(id))
  }
  const handleUpdate=(e,item)=>{
    dispatch(UpdateCartAsync({id:item.id,
      quantity: +e.target.value
    }))
  }

  return (
    <>
     {!items.length && <Navigate to="/" replace={true}></Navigate> }
     
     <div>
  <div className='mx-auto bg-white max-w-7xl px-4 sm:px-6 lg:px-8'>
 
  <div className="mt-8 max-w-7xl px-4 sm:px-6 lg:px-8">
  <h1 className="text-4xl my-5 font-bold tracking-tight text-gray-900">
              Cart
            </h1>
                    <div className="flow-root">
                      
   {status ==="loading"?<MagnifyingGlass
  visible={true}
  height="80"
  width="80"
  ariaLabel="magnifying-glass-loading"
  wrapperStyle={{}}
  wrapperClass="magnifying-glass-wrapper"
  glassColor="#c0efff"
  color="#e15b64"
  />:null
 }
                      <ul role="list" className="-my-6 divide-y divide-gray-200">
                        {items.map((item) => (
                          <li key={item.id} className="flex py-6">
                            <div className="h-24 w-24 flex-shrink-0 overflow-hidden rounded-md border border-gray-200">
                              <img
                                alt={item.product.title}
                                src={item.product.thumbnail}
                                className="h-full w-full object-cover object-center"
                              />
                            </div>

                            <div className="ml-4 flex flex-1 flex-col">
                              <div>
                                <div className="flex justify-between text-base font-medium text-gray-900">
                                  <h3>
                                    <Link to={item.product.thumbnail}>{item.product.title}</Link>
                                  </h3>
                                  <p className="ml-4">Rs{item.product ? discountedPrice(item.product) : 0}</p>
                                </div>
                                <p className="mt-1 text-sm text-gray-500">{item.product.brand}</p>
                              </div>
                              <div className="flex flex-1 items-end justify-between text-sm">
                                <div className="text-gray-500">
                                  <label 
                                  htmlFor='quantity'
                                  className='inline mr-5 text-sm font-medium leading-6 text-gray-900'>Qty</label>
                                  <select onChange={(e)=>handleUpdate(e,item)} value={item.quantity}>
                                  <option value="1">1</option>
                                  <option value="2">2</option>
                                  <option value="3">3</option>
                                  </select>
                                  </div>

                                <div className="flex">
                                <Modal
                            title={`Delete ${item.product.title}`}
                            message="Are you sure you want to delete this Cart item ?"
                            dangerOption="Delete"
                            cancelOption="Cancel"
                            dangerAction={(e) => handleRemove(e, item.id)}
                            cancelAction={()=>setOpenModal(null)}
                            showModal={openModal === item.id}
                          ></Modal>
                          <button
                            onClick={e=>{setOpenModal(item.id)}}
                            type="button"
                            className="font-medium text-indigo-600 hover:text-indigo-500"
                          >
                            Remove
                          </button>
                                </div>
                              </div>
                            </div>
                          </li>
                        ))}
                      </ul>
                    </div>
                  </div>
                
 <div className="border-t border-gray-200 px-4 py-6 sm:px-6">
                  <div className="flex justify-between my-2 text-base font-medium text-gray-900">
                    <p>Subtotal</p>
                    <p>Rs{totalAmount}</p>
                  </div>
                  <div className="flex justify-between my-2 text-base font-medium text-gray-900">
                    <p>Total items in cart</p>
                    <p>{totalItems} items</p>
                  </div>
                  <p className="mt-0.5 text-sm text-gray-500">Shipping and taxes calculated at checkout.</p>
                  <div className="mt-6">
                    <Link
                      to="/checkout"
                      className="flex items-center justify-center rounded-md border border-transparent bg-indigo-600 px-6 py-3 text-base font-medium text-white shadow-sm hover:bg-indigo-700"
                    >
                      Checkout
                    </Link>
                  </div>
                  <div className="mt-6 flex justify-center text-center text-sm text-gray-500">
                    <p>
                      or{' '}
                      <Link to="/">
                      <button
                        type="button"
                        onClick={() => setOpen(false)}
                        className="font-medium text-indigo-600 hover:text-indigo-500"
                      >
                        Continue Shopping
                        <span aria-hidden="true"> &rarr;</span>
                      </button>
                      </Link>
                    </p>
                  </div>
                </div>
  </div>
  </div>
  </>
  );
}
//
