import React, { useEffect, useState } from 'react';
import { useSelector, useDispatch } from 'react-redux';
import {
  fetchLoggedInOrderAsync,
  selectOrder,
  selectUserInfoStatus,
  
} from '../UserSlice';
import { discountedPrice } from '../../../app/constant';
import { MagnifyingGlass } from 'react-loader-spinner';


export default function UserOrder() {
  const dispatch = useDispatch();
 const status=useSelector(selectUserInfoStatus)
  const order = useSelector(selectOrder);

  useEffect(() => {
    
      dispatch(fetchLoggedInOrderAsync());
    
  }, [dispatch]);
 
  return (
    <>
    {order  && order.map((order)=>(
        <div>
             <div className='mx-auto bg-white max-w-7xl px-4 sm:px-6 lg:px-8'>
 
 <div className="mt-8 max-w-7xl px-4 sm:px-6 lg:px-8">
     <h3 className="text-sm my-5 font-bold tracking-tight text-gray-900">
             Order #{order.id}
           </h3>
           <h3 className="text-xl my-5 font-bold tracking-tight text-red-900">
             Order Status : {order.status}
           </h3>
           

                   <div className="flow-root">
                     <ul role="list" className="-my-6 divide-y divide-gray-200">
                       {order.items.map((item) => (
                         <li key={item.id} className="flex py-6">
                           <div className="h-24 w-24 flex-shrink-0 overflow-hidden rounded-md border border-gray-200">
                             <img
                               alt={item.product.title}
                               src={item.product.thumbnail}
                               className="h-full w-full object-cover object-center"
                             />
                           </div>

                           <div className="ml-4 flex flex-1 flex-col">
                             <div>
                               <div className="flex justify-between text-base font-medium text-gray-900">
                                 <h3>
                                   <a href={item.product.thumbnail}>{item.product.title}</a>
                                 </h3>
                                 <p className="ml-4">Rs{discountedPrice(item.product)}</p>
                               </div>
                               <p className="mt-1 text-sm text-gray-500">{item.product.brand}</p>
                             </div>
                             <div className="flex flex-1 items-end justify-between text-sm">
                               <div className="text-gray-500">
                                 <label 
                                 htmlFor='quantity'
                                 className='inline mr-5 text-sm font-medium leading-6 text-gray-900'>
                                    Qty:{item.quantity}</label>
                                
                                 
                                 </div>

                               <div className="flex">

                                 
                               </div>
                             </div>
                           </div>
                         </li>
                       ))}
                     </ul>
                   </div>
                 </div>
               
<div className="border-t border-gray-200 px-4 py-6 sm:px-6">
                 <div className="flex justify-between my-2 text-base font-medium text-gray-900">
                   <p>Subtotal</p>
                   <p>Rs{ order.totalAmount}</p>
                 </div>
                 <div className="flex justify-between my-2 text-base font-medium text-gray-900">
                   <p>Total items </p>
                   <p>{order.totalItems} items</p>
                 </div>
    <p className='mt-0.5 text-sm text-gray-500'>
      Shipping Address
       </p>
       <div className="flex justify-between gap-x-6 px-5 py-5 border-solid border-2 border-gray">
          <div className="flex min-w-0 gap-x-4"> 
         
            <div className="min-w-0 flex-auto">
              <p className="text-sm font-semibold leading-6 text-gray-900">{order.selectAddress.name}</p>
              <p className="mt-1 truncate text-xs leading-5 text-gray-500">{order.selectAddress.street}</p>
            </div>
          </div>
          <div className="hidden shrink-0 sm:flex sm:flex-col sm:items-end">
            <p className="text-sm leading-6 text-gray-900">{order.selectAddress.phone}</p>
            <p className="text-sm leading-6 text-gray-900">{order.selectAddress.pincode}</p>
            <p className="text-sm leading-6 text-gray-900">{order.selectAddress.city}</p>
            <p className="text-sm leading-6 text-gray-900">{order.selectAddress.state}</p>
          </div>
        </div>
              
               </div>
 </div>
        </div>
    ))}
     {status ==="loading"?<MagnifyingGlass
  visible={true}
  height="80"
  width="80"
  ariaLabel="magnifying-glass-loading"
  wrapperStyle={{}}
  wrapperClass="magnifying-glass-wrapper"
  glassColor="#c0efff"
  color="#e15b64"
  />:null
 }
    </>
  );
}